<template>
  <v-container fluid >    
    <v-row style="height:500px">
      <v-col
        v-for="item in layout"
        :key="item.i"
        :cols="12"
        :sm="12"
        :md="12"
        class="w-auto h-auto"
        :offset="item.i === 'chart4' ? 6: 0"
      >
        
        <v-card v-if="item.i === 'table3'">
          <v-card-title>
            <v-row dense justify="center">
              <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 2" >
                <v-select
                  :items="floorList"
                  item-text="FLOOR_NM"
                  item-value="FLOOR_ID"
                  label=""
                  v-model="selectedFloor"
                  outlined
                  dense
                >
                </v-select>
                
              </v-col>
              <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 2" >
                <v-select
                  :items="floorList"
                  item-text="SECTION_NM"
                  item-value="SECTION_ID"
                  label=""
                  v-model="selectedSection"
                  outlined
                  dense
                ></v-select>
              </v-col>
              <!-- <v-col :cols="$vuetify.breakpoint.mobile ? 2 : 3" >
                <v-text-field
                  :value="selectedFloorInfo ? selectedFloorInfo.data.power : ''"
                  label="  "
                  outlined
                  readonly
                ></v-text-field>
              </v-col> -->
              <v-col :cols="$vuetify.breakpoint.mobile ? 5 : 3" class="text-right" >
                <span class="clock"> : {{ dataCollectionTime }}</span>
              </v-col>
              <v-col :cols="$vuetify.breakpoint.mobile ? 5 : 5" class="d-flex justify-end ">
                <div class="ml-2"><v-btn color="primary darken-1" class="subtitle-1" @click="getHeaterList('C')"><v-icon>mdi-refresh</v-icon> </v-btn></div>
                <div v-if="isAdmin" class="ml-2"><v-btn color="orange darken-1" class="subtitle-1" @click="editFloor=true"><v-icon left>mdi-plus</v-icon></v-btn></div>
                <div class="ml-2"><v-btn color="error darken-1" class="subtitle-1" @click="deleteHeater"><v-icon left>mdi-minus</v-icon></v-btn></div>
                <div class="ml-2"><v-btn color="success darken-1" class="subtitle-1" @click="saveHeaterInfo"><v-icon style="padding-right:5px">mdi-content-save</v-icon> </v-btn></div>
              </v-col>
            </v-row> 
          </v-card-title>    
            <v-data-table
              :headers="headers"
              :items="heaterList"
              :page.sync="page"
              :footer-props="{ disablePagination : false, disableItemsPerPage : true }"
              :height="computedHeight(70)"
              show-select
              v-model="selected"
              item-key="DEVICE_ID"
            >
            
              <template v-slot:[`item.STATUS`]="{ item }">
                <v-chip
                  :color=" item.STATUS == 1 ? 'error' : item.STATUS == 4 ? 'success' : item.STATUS == 5 ? 'warning' : item.STATUS == 0 ? '#333' : '#000' "
                  label
                  small
                >
                  {{ item.STATUS_TEXT }}
                </v-chip>
              </template> 

              <template v-slot:[`item.FLOOR_NM`]="{ item }">
                <v-select
                  :items="floorList"
                  item-text="FLOOR_NM"
                  item-value="FLOOR_ID"
                  v-model="item.FLOOR_ID"
                  outlined
                  dense
                  hide-details
                  :disabled="!isAdmin"
                >
                <option value=""></option>
                </v-select>
              </template>

              <template v-slot:[`item.SECTION_NM`]="{ item }">
                <v-select
                  :items="floorList"
                  item-text="SECTION_NM"
                  item-value="SECTION_ID"
                  v-model="item.SECTION_ID"
                  outlined
                  dense
                  hide-details
                  :disabled="!isAdmin"
                >
                <option value=""></option>
                </v-select>
              </template>

              <template v-slot:[`item.ON_TEMP`]="{ item }">
                <v-text-field v-model="item.ON_TEMP" @keyup="$isNum2(item,'ON_TEMP')" :disabled="!isAdmin" suffix="C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field>                
              </template>
              <template v-slot:[`item.CURR_TEMP`]="{ item }">
                <v-text-field  v-model="item.CURR_TEMP" suffix="C"  dark dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field>                
              </template>
              <template v-slot:[`item.OFF_TEMP`]="{ item }">
                <v-text-field  v-model="item.OFF_TEMP" @keyup="$isNum2(item,'OFF_TEMP')" :disabled="!isAdmin" suffix="C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right"></v-text-field>                
              </template>
              <template v-slot:[`item.LOCATION`]="{ item }">
                <v-btn color="darken-1" class="subtitle-1" @click="findLocation(item)"></v-btn>
              </template>

              <template v-slot:footer>
                <v-container>
                    <v-row dense justify="center">
                      <v-col><v-text-field label=" " v-model="lowest" suffix="C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label=" " v-model="highest" suffix="C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label=" " v-model="average" suffix="C" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label=" "   v-model="current" suffix="kW" outlined error dense readonly hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label=" " v-model="onTemp" @keyup="$isNum1('onTemp')" :disabled="!isAdmin" suffix="C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>
                      <v-col><v-text-field label=" " v-model="offTemp" @keyup="$isNum1('offTemp')" :disabled="!isAdmin" suffix="C" outlined dark dense hide-details width="35" class="pa-1" style="text-align: right" ></v-text-field></v-col>                      
                    </v-row>
                </v-container>
            </template>
            </v-data-table>
          </v-card>        
      </v-col>
    </v-row>
    
    <!--         yellow darken-1 -->
    <v-dialog v-model="editFloor" max-width="48dvw" persistent >
      <v-card outlined>
        <v-card-title>
          <v-row class="rounded text-left">
            <v-col :cols="8" >
              <!-- <span class="headline"></span> -->
              <h3 class="ml-2"></h3>
            </v-col>
            <v-col :cols="4" class="d-flex justify-end ">                                        
              <v-btn color="secondary" :loading="loading"  @click="editFloor = false"></v-btn>
              <v-btn class="ml-2" color="primary darken-1" :loading="loading"  @click="saveFloor('U')"></v-btn>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row dense>
              <v-col cols="2" >
                <v-select
                  :items="['','']"
                  label=""
                  v-model="floorType"
                  outlined
                  hide-details
                  dense
                >
                </v-select>
              </v-col>
              <v-col cols="2" >
                <v-text-field label="" readonly required outlined dark dense hide-details width="100" ></v-text-field>
                <!-- <v-select
                  :items="sectionList"
                  item-text="sectionNm"
                  item-value="sectionId"
                  label=" "
                  v-model="sectionType"
                  outlined
                  dense
                ></v-select> -->
              </v-col>
              <v-col cols="2" >                
                <v-text-field v-model="floorFrom" @keyup="$isNum1('floorFrom')" label="" required outlined dark dense hide-details width="100" ></v-text-field>
              </v-col>
              <v-col cols="2" >                
                <v-text-field v-model="floorTo" @keyup="$isNum1('floorTo')" label="" required outlined dark dense hide-details width="100" ></v-text-field>                
              </v-col>
              <!-- <v-col cols="3" >                
                <v-text-field v-model="floorNm" label="" outlined dense hide-details width="100" class="pa-1" ></v-text-field>
              </v-col> -->
              <v-col cols="3" class="mt-1 d-flex justify-end ">
                
                    <v-btn color="orange darken-2" :loading="loading"  @click="createFloor"></v-btn>

                    <v-btn class="ml-2" color="error" :loading="loading" @click="saveFloor('D')"></v-btn>
                  
              </v-col>
            </v-row>
            <v-row dense>
              <v-col cols="12" >
                <!--   -->
                <v-data-table
                  dense
                  divider
                  fixed-header
                  show-select
                  v-model="floorChkList"
                  :headers="floorHeaders"
                  :items="floorList"
                  item-key="FULL_ID"
                  :items-per-page="10"
                  :footer-props="{ itemsPerPageOptions: [10,20,30,50,'All'] }"
                  >
                    <!-- <template v-slot:[`item.chk`]="{ item }">
                      <input type="checkbox" class="mt-2" style="transform: scale(1.5)" v-model="item.chk" >
                    </template> 
                    <template v-slot:[`item.floorType`]="{ item }">
                      <v-select
                        :items="floorList"
                        item-text="floorNm"
                        item-value="floorId"
                        v-model="item.floorId"
                        outlined
                        dense
                        hide-details
                      ></v-select>
                    </template>-->
                    <template v-slot:[`item.FLOOR_NM`]="{ item }">
                      <v-text-field v-model="item.FLOOR_NM" outlined dark dense small hide-details width="50%" class="pa-1" ></v-text-field>
                    </template>
                    <template v-slot:[`item.SECTION_NM`]="{ item }">
                      <v-text-field v-model="item.SECTION_NM" outlined dark dense small hide-details width="50%" class="pa-1" ></v-text-field>
                    </template>
                    
                  </v-data-table>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="80"
        width="10"
        color="primary lighten-2"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import DynamoDBService from '@/services/DynamoDBService.js';
import { mapState , mapMutations} from "vuex";
import moment from 'moment';

export default {  
  data() {
    return {
      loading: false,
      isDraggable: false,
      editFloor:false,
      editHeater:false,
      selected: [],
      floorHeaders: [        
        { text: "", value: "FLOOR_NM" ,width:"50%"},
        { text: "", value: "SECTION_NM" ,width:"50%"}
      ],
      floorList: [],
      floorChkList: [],
      floorType:"",
      floorNm:"",
      sectionNm:"",
      floorFrom:1,
      floorTo:5,
      floorId: "",
      sectionId: "",
      selectedFloor: null, //   floorId
      selectedSection: null, //  sectionId
      heaterList: [],
      dataCollectionTime: '00:00:00',
      layout: [
        { x: 0, y: 0, w: 12, h: 8, i: "table3" },
        { x: 0, y: 4, w: 12, h: 8, i: "chart1" },
      ],      
      onTemp : null,
      offTemp : null,
      lowest: 0,
      highest: 0,
      average: 0,
      current: 0,
      headers: [
        //{ text: "No.", value: "no", align: 'center' , sortable: false, width:"70" },         
        { text: "", value: "DEVICE_ID",  width:"120"},
        { text: "", value: "FLOOR_NM", width:"15%"},
        { text: "", value: "SECTION_NM", width:"15%"},
        { text: "", value: "ON_TEMP" , width:"120" },
        { text: "", value: "CURR_TEMP" , width:"100"},
        { text: "", value: "OFF_TEMP" , width:"120"},
        { text: "", value: "STATUS" , width:"80"},  
        { text: "", value: "CURRENT" , width:"80"},
        { text: "", value: "LOCATION" , width:"80"}
      ],
      //lastRcvTime: [],
      pagination: {rowsPerPage: 10},
      page: 1,
      newItems: [],
    };
  },
  computed: {
    //...mapState('wssMsg',['msg','isConn']),
    ...mapState('userInfo',['getUser','isAdmin']),
    

  },
  watch: {
  //  msg(newVal){
  //     if (newVal) {
  //       const timestamp = new Date().toLocaleString();
  //       this.dataCollectionTime = timestamp;
        
  //       // lastRcvTime terminalId   () 
  //       const newData = JSON.parse(newVal) || {};
  //       this.lastRcvTime.forEach((item) => {
  //         if( newData.terminalId === item.terminalId ){         
  //           item.rcvTime = moment().format('HHmmss');
  //           console.log("\n-- lastRcvTime updated!! : ",newData.terminalId,item.terminalId)
  //         }
  //       });

  //       // lastRcvTime  newVal.terminalId  
    //       let found = this.lastRcvTime.find(item => item.terminalId === newData.terminalId);
    //       if( !found ){
    //         console.log("\n-- lastRcvTime added!! : ",newData.terminalId,found)
    //         this.lastRcvTime.push({terminalId:newData.terminalId,rcvTime:moment().format('HHmmss')});
    //       }

  //       this.updateHeaterInfo(newVal);
  //     }
  //   },
    
    
  //   isConn(stat){      
  //       // stat   heaterList  connect   'Y'  'N'  
  //       console.log("\n--- HeaterManage isConn",stat)
  //       this.heaterList.forEach((heater) => {
  //         if( !stat ){
  //           heater.connect = 'N';
  //           heater.msg = `[]-${heater.DEVICE_ID}`
  //           // History  
  //           this.saveHistory(heater);

  //         }
  //       });              
  //   },
   
    selectedFloor(newVal) {
      console.log(newVal);
      this.floorId = newVal;
      this.getHeaterList(); 
    },

    editFloor(newVal){
      if( !newVal ){
        this.getFloorList();
        this.getHeaterList();
      }
      this.floorList = this.floorList.filter((item) => item.FLOOR_ID !== "");
    },
    editHeater(){
      
      alert(this.heaterChecked);
      //console.log(this.heaterList[idx]);
    },

    selectedSection(newVal) {
           
      this.sectionId = newVal;
      this.getHeaterList(); 
    },

  },

  
  methods: {    
    ...mapMutations(['setShowMsg']),
    isNumber(evt) {
      console.log("-- isNumber",evt)

      if( isNaN(evt) ) {
        // evt     
         evt = evt.replace(/[^0-9]/g,'');
      }

      console.log("-- replace ",evt)

    },
    findLocation(item){
      this.loading = true;
      console.log(item.DEVICE_ID);
      DynamoDBService.updatecommand(item.DEVICE_ID);
      setTimeout(() => this.loading = false , 1000);
      
    },
    calculateCols(item) {
      //         
      return this.$vuetify.breakpoint.mdAndUp ? item.w : 12;
    },
    getFloorList() {
      this.loading = true;
      
      DynamoDBService.getFloorList().then((res) => {
        this.floorList = res;
        let arr = {
          "FLOOR_ID": "",
          "FLOOR_NM": "",
          "SECTION_ID":"",
          "SECTION_NM":""
        };
        this.floorList.unshift(arr);
      });
      this.loading = false;
    },
    getNewHeaterList(){
      let param = {
          "FLOOR_ID": this.floorId,
          "SECTION_ID": this.sectionId,
          "LAST_DATE": this.dataCollectionTime
      };
      this.loading = true;
      DynamoDBService.getNewHeaterList(param).then((res) => {
        res.forEach((item)=>{
          this.heaterList.unshift(item);
        })
        this.loading = false;
      }).then(() =>{this.getHeaterList();});
    },
    getHeaterList(cmd) {
      let param = {
          "FLOOR_ID": this.floorId,
          "SECTION_ID": this.sectionId,
      };
      this.loading = true;
      //      heaterList 
      //      
      //   this.heaterList.push()
      console.log(cmd);
      DynamoDBService.getHeaterList(param).then((res) => {
        if('C' != cmd && this.heaterList.length !== 0){
          for(var i=0; i<this.heaterList.length; i++){
            for(var j=0; j<res.length; j++){
              const item = res[j];
              if(this.heaterList[i].DEVICE_ID == item.DEVICE_ID){
                this.heaterList[i].CURR_TEMP = item.CURR_TEMP;
                this.heaterList[i].STATUS = item.STATUS;
                this.heaterList[i].STATUS_TEXT = item.STATUS_TEXT;
                this.heaterList[i].CURRENT = item.CURRENT;
                
               // const idx = res.findIndex(function(i) {return i.DEVICE_ID === item.DEVICE_ID}) // findIndex = find + indexOf
               // if (idx > -1) res.splice(idx, 1)
                break;
              }
            }
          }
        }else{
          this.heaterList = res;
          this.onTemp = null;
          this.offTemp = null;
        }
        this.loading = false;
      });
    },
    getRandomNumber(numbers) {
        // let numbers = [26, 27, 28];
        let randomIndex = Math.floor(Math.random() * numbers.length);
        return numbers[randomIndex];
    },
    /*
    updateHeaterInfo() {
      const newData = JSON.parse(this.msg[0]) || {};
      this.totKw = 0;
      this.totDeg = 0;

      // socket.io  newData  parsing  currTemp  heaterList  currTem  
      this.heaterList.forEach((heater) => {
        // console.log("-- heater.deviceId",heater.deviceId)
        // console.log("-- newData.deviceId",newData.deviceId)
        
        //const found = newData[heater.deviceId];
        //console.log("-- found",found)
        if (heater.terminalId === newData.terminalId) {
          //heater.currTemp = this.getRandomNumber([26, 27, 28, 27, 27, 27, 27]); // newData.currTemp;
          //heater.power = this.getRandomNumber([2.1,2.8,1.8,2.3,2.3,2.3,2.3]); // newData.power;
          heater.currTemp = newData.currTemp;
          heater.power = newData.power;
          heater.msg = newData.msg;

          heater.connect = 'Y';
        }

        this.totDeg += heater.currTemp;
        this.totKw += (heater.power || 0 );
      });

      // this.totDeg this.totDeg   ( ,   )
      if( this.heaterList.length ) this.totDeg = Math.round(this.totDeg / this.heaterList.length);
      // this.totKw  Ampare   kW    1  
      this.totKw = Math.round(this.totKw * 220 / 1000 * 10) / 10;
      
      
    },
    */
    computedWidth(p){
      //       
      const screenWidth = document.querySelector(".v-main__wrap").clientWidth;
      // console.log("@ computedWidth - screenWidth : ",screenWidth);
      // console.log("@ computedWidth : ",Math.floor(screenWidth * (p*0.01)));
      return Math.floor(screenWidth * (p*0.01)); //  50:   50% 
    },
    computedHeight(p) {
      //       
      const screenHeight = document.querySelector(".v-main__wrap").clientHeight;
      // console.log("@ computedHeight - screenHeight : ",screenHeight);
      // console.log("@ computedHeight : ",Math.floor(screenHeight * (p*0.01)));
      return Math.floor(screenHeight * (p*0.01)); //  50:   50% 
    },
    saveHeaterInfo() {
      //   heaterList.data.onTemp ,heaterList.data.offTemp  totOnTemp, totOffTemp  
      console.log(this.onTemp);
      console.log(this.offTemp);
      this.heaterList.forEach((heater) => {
        heater.ON_TEMP = this.onTemp || heater.ON_TEMP ;
        heater.OFF_TEMP = this.offTemp || heater.OFF_TEMP;
      });
      
      //   
      DynamoDBService.saveHeaterList(this.heaterList).then((res) => {
        console.log("    : ",res);
        this.setShowMsg({isVisible:true,color:'primary',msg:' .'});
//        this.getFloorList();
        this.getHeaterList('C');
      });

      this.onTemp = null;
      this.offTemp = null;
    },   
    deleteHeater(){
      this.loading = true;
      //     . 
        if (this.selected.length === 0) {
          this.setShowMsg({isVisible:true,color:'error',msg:'  '});
          return;
        }
        let delHeaterList = '';

        this.selected.forEach((item) =>{
          delHeaterList += '\'' + item.DEVICE_ID + '\',';
        });
        console.log(" " + delHeaterList);
        delHeaterList = delHeaterList.slice(0, -1);
        DynamoDBService.deleteHeater(delHeaterList).then(() => {
          this.getHeaterList('C'); 
      })
      .finally(() => { 
        this.loading = false; 
        this.setShowMsg({isVisible:true,color:'primary',msg:' .'});
      });
    },
    createFloor() {
      let floorTypeCd = "B"
      let floorNm = this.floorNm;
      let floorFrom = this.floorFrom || 0;
      let floorTo = this.floorTo || 0;

      // from ,to  0   null  
      if (floorFrom <= 0 || floorTo <= 0) {
        this.setShowMsg({isVisible:true,color:'error',msg:'  0 .'});
        return;
      }
      //from  to   
      if (floorFrom > floorTo) {
        this.setShowMsg({isVisible:true,color:'error',msg:'  .'});
        return;
      }

      floorTypeCd = this.floorType === "" ? "B" : "F";

      // floorFrom  floorTo  floorList     
      let dupCnt = 0;
      for (let i = floorTo; i >= floorFrom; i--) {
        for(let j=floorTo; j >= floorFrom; j--){

          // floorList floorId   continue
          if (this.floorList.find((item) =>  item.FLOOR_ID+item.SECTION_ID === `${floorTypeCd}${i}+S${j}`) ) {
            dupCnt++;
            continue;
          }

          this.floorList.unshift({
            FLOOR_ID: `${floorTypeCd}${i}`, 
            SECTION_ID: `S${j}`, 
            FLOOR_NM: `${this.floorType}${i} ${floorNm}`,
            SECTION_NM: `${j}`
          });
        }
      }

      let param = "";
      if (dupCnt > 0) { 
        // confirm 
        param = {isVisible:true,color:'warning',msg:`${floorFrom} ~ ${floorTo}  ${dupCnt}   .\n?`}
      } else {
        // confirm   n  ?
        param = {isVisible:true,color:'warning',msg:` ${floorTo - floorFrom + 1}    ?`}
      }

      //   
      if (confirm(param.msg)) {
       // this.saveFloor('U');
      }
    },
    saveFloor(cmd) {

      let paramList = [];
  
      // cmd  D  ? confirm 
      if (cmd === 'D') {
        
        //     . 
        if (this.floorChkList.length === 0) {
          this.setShowMsg({isVisible:true,color:'error',msg:'  '});
          return;
        }

        let param = {isVisible:true,color:'warning',msg:`  ?`}

        //     "    ?"      ""    
        let found = this.heaterList.find(item => this.floorChkList.find(chk => chk.FLOOR_ID+chk.SECTION_ID === item.FLOOR_ID+item.SECTION_ID));
        let heaterCnt = 0;
        if( found ){
          param = {isVisible:true,color:'warning',msg:`   .\n?(  )`}
          heaterCnt = 1;
        }

        //   
        if (!confirm(param.msg)) {
          return;
        }
        // floorChkList  selectedFloor   floorList  
        paramList = this.floorChkList.map((item) => {
          console.log(item);
          return {
            FLOOR_ID: item.FLOOR_ID,
            SECTION_ID: item.SECTION_ID,
            FLOOR_NM: item.FLOOR_NM,
            SECTION_NM: item.SECTION_NM,
            heaterCnt: heaterCnt
          };
        });
      }else{
        paramList = this.floorList;
        // for(var i=0; i<this.floorList.length; i++){
        //   paramList.push(
        //       {
        //         FLOOR_ID: this.floorList.FLOOR_ID, 
        //         SECTION_ID: this.floorList.SECTION_ID,
        //         FLOOR_NM: this.floorList.FLOOR_NM, 
        //         SECTION_NM: this.floorList.SECTION_NM
        //       }
        //     )
        // }
      }

      this.loading = true;

      if( paramList.length === 0 ){
        this.setShowMsg({isVisible:true,color:'error',msg:' .'});
        this.loading = false;
        return;
      }
       
      // 
      DynamoDBService.saveFloorList(paramList,cmd).then(() => {
        this.setShowMsg({isVisible:true,color:'primary',msg:' .'});
        //this.getFloorList();
         this.editFloor = false;
      })
      .catch((err) => {
        console.log(err);
        this.setShowMsg({isVisible:true,color:'error',msg:' .'});        
      })
      .finally(() => {
        this.loading = false;
        this.floorList = [];
      });
      
    },
    showErrorPopup(errMsg) {
      errMsg = " \n \n010-1111-2222\n\n" + errMsg;
      let param = {isErrorMsg:true,color:'error',msg:errMsg}
      this.setShowMsg(param);
    },
    saveHistory(heater) {
      // History   id  
      // delete heater.id;
      DynamoDBService.saveHistory(heater).then((res) => {
        console.log("\n--- History  : ",res);
      })
      .catch((err) => {
        console.log("\n--- History  ! " ,err);
      })
    },

    //  , , 
    getNewDate(){
      DynamoDBService.getNewHistory().then((res) => {
        this.lowest = res[0].lowest;
        this.highest = res[0].highest;
        this.average = res[0].highest;
        this.current = res[0].CURRENT;
        this.dataCollectionTime = res[0].createdAt
      });
    }
  },
  mounted() {
    // Floor List
    this.getNewDate();
    this.getFloorList();
    this.getHeaterList();
    


    // heaterList   5 lastRcvTime  terminalId   1   connect  'N'  
    this.intvId = setInterval(() => {
      this.getNewDate();
      this.getNewHeaterList();
      
      if( this.heaterList.length ){
        this.heaterList.forEach((heater) => {
          let currTime = moment().format('HHmmss');
          let arr = (heater.updatedAt).split(' ');
          let rcvTime = arr[1].replaceAll(':', ' ');

          let diff = currTime - rcvTime;
          if(diff > (60*10)){
            heater.STATUS = 5;
            this.saveHistory(heater);
            this.saveHeaterList(heater);
          }

          
          // const found = this.lastRcvTime.find(item => item.DEVICE_ID === heater.DEVICE_ID);
          // // console.log("-- 1  heater.terminalId : ",heater.terminalId)
          // if( found ){
          //   let rcvTime = fou-nd.rcvTime;
          //   let currTime = moment().format('HHmmss');

          //   let diff = currTime - rcvTime;            
          //   console.log("-- diff",diff)
          //   if( diff > 60 ){
          //     heater.connect = 'N';
          //     heater.msg = `[${Math.floor(diff/60)} ]`

          //     // History  
          //     this.saveHistory(heater);

          //     // history   lastRcvTime  
          //     const idx = this.lastRcvTime.findIndex(item => item.DEVICE_ID === heater.DEVICE_ID);
          //     if( idx > -1 ){
          //       this.lastRcvTime.splice(idx,1);
          //     }

          //   }
          // }
        });
      }
    }, 5000);

  },
  beforeDestroy() {
    clearInterval(this.intvId);
  },
};
